$biggest-font: 'Lato-black';
$medium-font: 'Lato';
$smallest-font: 'Lato';
$marker-size: 2vh;
$horizon-margin: 2vh 0; 
$vert-margin: 1vh 0;


.App {
  text-align: center;
  font: .8rem $smallest-font;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

//components and stuff

#marker {
  width: $marker-size;
}

.navbar-start {
  margin: auto;
  font-family: $smallest-font;
}

#myName {
  font: 4rem $biggest-font;
  letter-spacing: 0rem;
  text-transform: none; 
  max-width: 50%;
}

.title {
  font: 1rem $medium-font;
  letter-spacing: .6rem;
  text-transform: uppercase;  
}

.subtitle {
  font-family: $medium-font;
}

#skills li {
  display: inline; 
  padding: 0 1vh;
  margin: $horizon-margin;
}

.columns {
  margin: $horizon-margin;
}

#contact-info li {
  margin: $vert-margin;
}

.image.is-64x64 {
  margin: auto;
}

.level.is-mobile .level-item:not(:last-child) {
  margin-bottom: 0;
  margin-right: 0;
}

