// timeline stuff -----------
.timeline {
  position: relative;
  max-width: 100%;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 1vh;
  background-color: orange;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -.4%;
  height: 100%;
}

